var THREE = require('three');

var colorsDefault = {
  InteriorWall: 0x008000,
  ExteriorWall: 0xFFB400,
  Roof: 0x800000,
  InteriorFloor: 0x80FFFF,
  ExposedFloor: 0x40B4FF,
  Shade: 0xFFCE9D,
  UndergroundWall: 0xA55200,
  UndergroundSlab: 0x804000,
  Ceiling: 0xFF8080,
  Air: 0xFFFF00,
  UndergroundCeiling: 0x408080,
  RaisedFloor: 0x4B417D,
  SlabOnGrade: 0x804000,
  FreestandingColumn: 0x808080,
  EmbeddedColumn: 0x80806E,
  Undefined: 0x88888888
};
/**
 * Returns vertices from a gbXML polyloop object
 * @param {Object} polyloop gbXML polyloop object
 * @returns {Array} An array of points
 */

var getVertices = function getVertices(polyloop) {
  var points = polyloop.CartesianPoint.map(function (CartesianPoint) {
    return new THREE.Vector3().fromArray(CartesianPoint.Coordinate);
  });
  return points;
};
/**
 * Returns a plane from an array of points
 * @param {Array} points An array of points 
 * @param {Number} start An integer value
 * @returns {Object} A ThreeJS plane object
 */


var getPlane = function getPlane(points) {
  var start = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : 0;
  var triangle = new THREE.Triangle();
  triangle.set(points[start], points[start + 1], points[start + 2]);
  var plane = triangle.getPlane(new THREE.Plane());

  if (triangle.getArea() === 0) {
    // looks like points are colinear therefore try next set
    var next = start + 1;
    plane = getPlane(points, next);
  }

  return plane;
};
/**
 * Returns a 2D shape mesh from vertices, materials and holes
 * @param {Array} vertices An array of vertices
 * @param {Object} material A ThreeJS Material object
 * @param {Array} holes An array of hole objects
 * @returns {Object} A ThreeJS Mesh object
 */


var get2DShape = function get2DShape(vertices, material) {
  var holes = arguments.length > 2 && arguments[2] !== undefined ? arguments[2] : [];
  var shape = new THREE.Shape(vertices);
  shape.holes = holes;
  var geometryShape = new THREE.ShapeGeometry(shape);
  var shapeMesh = new THREE.Mesh(geometryShape, material);
  return shapeMesh;
};
/**
 * Returns a 3D shape mesh from vertices, materials and holes
 * @param {Array} vertices An array of vertices
 * @param {Object} material A ThreeJS Material object
 * @param {Array} holes An array of hole objects
 * @returns {Object} A ThreeJS Mesh object
 */


var get3dShape = function get3dShape(vertices, material) {
  var holes = arguments.length > 2 && arguments[2] !== undefined ? arguments[2] : [];
  var plane = getPlane(vertices);
  var referenceObject = new THREE.Object3D(); // copy the rotation of the plane

  referenceObject.lookAt(plane.normal); // figure out the angle it takes to rotate the vertices so they lie on the XY plane

  referenceObject.quaternion.conjugate();
  referenceObject.updateMatrixWorld(); // What does this line do? If it is modifying inline then forEach() is preferable to map()

  vertices.map(function (vertex) {
    return referenceObject.localToWorld(vertex);
  });
  var holeVertices = holes.map(function (verticesHoles) {
    // TODO: Clarify what the line below does...
    verticesHoles.map(function (vertex) {
      return referenceObject.localToWorld(vertex);
    });
    var hole = new THREE.Path();
    hole.setFromPoints(verticesHoles);
    return hole;
  });
  var shapeMesh = get2DShape(vertices, material, holeVertices);
  shapeMesh.lookAt(plane.normal);
  var center = plane.coplanarPoint(new THREE.Vector3());
  shapeMesh.position.copy(center);
  return shapeMesh;
};
/**
 * Returns ThreeJS Meshes from an array of surfaces
 * @param {Array} surfaces An array of surface object
 * @returns {Array} An array of ThreeJS Meshes
 */


var getSurfaceMeshes = function getSurfaceMeshes(surfaces) {
  var surfaceMeshes = [];
  surfaces.forEach(function (surface) {
    var holes = [];
    var openings = surface.Opening;

    if (openings) {
      openings = Array.isArray(openings) ? openings : [openings];
      openings.forEach(function (opening) {
        var polyloop = opening.PlanarGeometry.PolyLoop;
        var vertices = getVertices(polyloop);
        holes.push(vertices);
      });
    }

    var polyloop = surface.PlanarGeometry.PolyLoop;
    var vertices = getVertices(polyloop); // eslint-disable-next-line max-len

    var color = colorsDefault[surface.surfaceType] ? colorsDefault[surface.surfaceType] : colorsDefault.Undefined;
    var material = new THREE.MeshPhongMaterial({
      color: color,
      side: 2,
      opacity: 0.85,
      transparent: true
    });
    var shape = get3dShape(vertices, material, holes);
    shape.userData.data = surface;
    shape.castShadow = true;
    shape.receiveShadow = true;
    surfaceMeshes.push(shape);
  });
  return surfaceMeshes;
};
/**
 * Returns the edges of an array of Meshes
 * @param {Array} surfaceMeshes An array of ThreeJS Meshes
 * @returns {Array} An array of edge objects 
 */


var getSurfaceEdges = function getSurfaceEdges(surfaceMeshes) {
  var lineMaterial = new THREE.LineBasicMaterial({
    color: 0x888888
  });
  console.log(surfaceMeshes);
  var surfaceEdges = surfaceMeshes.children.map(function (mesh) {
    // The line below doesn't look right. What is it doing?
    mesh.userData.edges = mesh;
    var edgesGeometry = new THREE.EdgesGeometry(mesh.geometry);
    var surfaceEdge = new THREE.LineSegments(edgesGeometry, lineMaterial);
    surfaceEdge.rotation.copy(mesh.rotation);
    surfaceEdge.position.copy(mesh.position);
    return surfaceEdge;
  });
  return surfaceEdges;
};
/**
 * Returns the openings from a surface
 * @param {Object} surfacesJson A surface object
 * @returns {Array} An array of surface opening objects
 */


var getOpenings = function getOpenings(surfacesJson) {
  var surfaceOpenings = [];
  var material = new THREE.MeshBasicMaterial({
    color: 0x000000,
    opacity: 0.5,
    side: 2,
    transparent: true
  });
  surfacesJson.forEach(function (surfJson) {
    var openings = surfJson.Opening ? surfJson.Opening : [];
    openings = Array.isArray(openings) ? openings : [openings];
    openings.forEach(function (opening) {
      var points = getVertices(opening.PlanarGeometry.PolyLoop);
      var shapeMesh = get3dShape(points, material);
      shapeMesh.userData.data = opening;
      surfaceOpenings.push(shapeMesh);
    });
  });
  return surfaceOpenings;
};

module.exports = {
  getVertices: getVertices,
  getPlane: getPlane,
  get2DShape: get2DShape,
  get3dShape: get3dShape,
  getSurfaceMeshes: getSurfaceMeshes,
  getSurfaceEdges: getSurfaceEdges,
  getOpenings: getOpenings
};